@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800&display=swap);
html,body,#root{margin:0;padding:0;height:100%;width:100%}body{overflow:hidden}#root{font-family:'Raleway', sans-serif;color:#505050;font-size:18px;line-height:28px;overflow:auto;display:flex;min-height:100%}code{background-color:#dedede;padding:3px 7px;color:#bc451a}*{box-sizing:border-box}b{font-weight:600}a{color:#1abc9c}h1{text-transform:uppercase;font-weight:800;color:#777}#links{margin-bottom:2rem;padding:2rem;width:30%}#links a{display:block;margin-bottom:0.5rem}main{padding:2rem;flex:1 1;width:70%}button{background:#1abc9c;border:2px solid #1abc9c;border-radius:2rem;padding:1.2rem 2.5rem;font-size:inherit;text-transform:uppercase;color:white}button:disabled{opacity:0.6}button:hover{background:none;color:#1abc9c;padding:1.2rem 2.3rem}button.primary{margin:0 auto;display:block;transition:all 0.3s ease-in-out}input{width:100%;font-family:inherit;border:1px solid #b39f98}input:active,input:focus{outline:1px solid #f93}@media all and (max-width: 950px){#links,main{width:auto}#links{margin-bottom:0}#root{flex-direction:column}}

.sample-page>header{margin-bottom:2.5rem}.sample-page>header h1{margin:0;margin-bottom:0.7rem;padding:0}.sample-page>header .description{font-weight:300}.sample-page em{color:#bc451a;font-style:normal;font-weight:600}

.loader{font-size:10px;margin:50px auto;text-indent:-9999em;width:11em;height:11em;border-radius:50%;background:#1abc9c;background:linear-gradient(to right, #1abc9c 10%, rgba(255,255,255,0) 42%);position:relative;animation:load3 1.4s infinite linear;transform:translateZ(0)}.loader:before{width:50%;height:50%;background:#1abc9c;border-radius:100% 0 0 0;position:absolute;top:0;left:0;content:''}.loader:after{background:white;width:75%;height:75%;border-radius:50%;content:'';margin:auto;position:absolute;top:0;left:0;bottom:0;right:0}@keyframes load3{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.person-table-id-text{font-size:0.8rem;opacity:0.8;display:block}

